<template>
    <div>
        <top-nav title="订单列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).拼团订单需要 拼团成功</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="订单ID">
                        <el-input v-model="search.order_id" value=""></el-input>
                    </el-form-item>

                    <el-form-item label="订单编号">
                        <el-input v-model="search.order_sn" value=""></el-input>
                    </el-form-item>

                    <el-form-item label="收货人">
                        <el-input v-model="search.name" value=""></el-input>
                    </el-form-item>

                    <el-form-item label="收货人联系方式">
                        <el-input v-model="search.mobile" value=""></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="mini" type="primary" @click="getOrderList(1)" icon="el-icon-search">搜索</el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column width="80" prop="order_id" label="订单ID"></el-table-column>
                    <el-table-column width="150" prop="order_sn" label="订单号" ></el-table-column>
                    <el-table-column  prop="name" label="收货人"></el-table-column>
                    <el-table-column  label="物流费用">
                        <template slot-scope="scope">
                            ￥ {{scope.row.shipping_price}}
                        </template>
                    </el-table-column>
                    <el-table-column  prop="goods_price" label="订单金额">
                        <template slot-scope="scope">
                            ￥ {{scope.row.goods_price}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="add_time" label="下单时间"></el-table-column>

                    <el-table-column prop="pay_time" label="支付时间"></el-table-column>

                    <el-table-column width="300" label="操作">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <router-link :to="'/delivery/order/view/'+scope.row.order_id">
                                    <el-button type="text" icon="el-icon-view">去发货</el-button>
                                </router-link>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="getOrderList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    export default {
        name: "customerIndex",
        components: { TopNav},
        data: function () {
            return {
                //整体菊花
                select:1,
                loading:false,
                search:{
                    date:[],
                    order_id: '',
                    order_sn: '',
                    mobile: '',
                    name: '',
                },
                status:{},
                datas:{},
                userList:[],
            };
        },
        created() {
            this.getOrderList();
        },
        methods: {
            //获取订单列表
            getOrderList(page=1){
                this.loading = true;
                api.post("/shop/admin.order.delivery/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    order_id: this.search.order_id,
                    order_sn: this.search.order_sn,
                    mobile: this.search.mobile,
                    name: this.search.name,
                }).then(res => {
                    this.loading = false;
                    this.datas = res.list;
                    if(page <= 1){
                        this.status = res.status;
                    }
                }).catch(() => {
                    this.loading = false;
                });
            }

        }
    };
</script>
